import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, LoadingController, AlertController, ToastController, Toast } from 'ionic-angular';
import { SoapService } from '../soap.service';
import { Storage } from '@ionic/storage';
import { api_base_url, api_user, api_pass, sc_code, sc_type } from '../../config'; 

/**
 * Generated class for the IpcContactRatingPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({ 
  selector: 'page-ipc-contact-rating',
  providers: [SoapService],
  templateUrl: 'ipc-contact-rating.html',
})
export class IpcContactRatingPage {
  rate:any = '';
  data:any;
  userdataTPK:any;

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public viewCtrl:ViewController,
    public soapService:SoapService,
    public loadingCtrl: LoadingController,    
    public storage: Storage,
    public alertCtrl: AlertController,
    public toastCtrl: ToastController
  ) {
    this.data = navParams.get('data');
    this.storage.get('userdataTPK').then((val) => {
      this.userdataTPK = val;
    });

  }

  onModelChange() {
    // console.log(this.rate);
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad IpcContactRatingPage');
  }

  submit() {
    let loading = this.loadingCtrl.create({
      spinner: 'dots',
      content: "mohon tunggu...",       
      cssClass: 'transparent',
      //dismissOnPageChange:true
    });
    loading.present();    
    this.soapService    
    .post(api_base_url,'eoffice_rating_ipcc',{fStream:JSON.stringify(
      {
        usernameEDI : api_user, 
        passwordEDI : api_pass  ,      
        rate: this.rate,
        id_pengajuan: this.data['ID_PENGAJUAN']        
      }
    )}).then(result => {
      var responData = JSON.parse(String(result));
      // console.log(responData);
      if (responData['rcmsg'] == "SUCCESS") {
        let toast = this.toastCtrl.create({
          message: 'Close Case Berhasil !',
          duration: 3000,
          position: 'bottom'
        });
        toast.present().then(() => {
          this.pushNotif();
          this.viewCtrl.dismiss({
            isRate: true
          });
        });

      } else {        
        let toast = this.toastCtrl.create({
          message: 'Mohon Maaf Sedang Terjadi Kesalahan, Coba Beberapa Saat Lagi.',
          duration: 3000,
          position: 'bottom'
        });
        toast.present(); 
      }  
      loading.dismiss();       
    })
    .catch(error => {
      // console.log(error);
      let toast = this.toastCtrl.create({
        message: 'Terjadi Masalah Koneksi, Silahkan Coba Kembali.',
        duration: 3000,
        position: 'bottom'
      });
      toast.present();

      loading.dismiss();            
      
    });
  }

  pushNotif() {    
    this.soapService
    .post(api_base_url,'eoffice_notif_imove_nipp',{fStream:JSON.stringify(
      {
        usernameEDI : api_user, 
        passwordEDI : api_pass,
        nipp		: [],
        data		: {
          "res": "IpcContactQuestionListPage"
        },
        content	: {
          "en":"Permasalahan Telah Ditutup Oleh " + this.userdataTPK['data']['NAMA']
        },
        heading : {
          "en": "Rating"
        },
        id_kategori: this.data['KATEGORI']
      }
    
    )}).then(result => {
      let hasil = JSON.parse(String(result));
      // console.log("push notif : " + hasil);            
      this.navCtrl.pop();
    }).catch(error => {        
      // console.log(error);            
      this.navCtrl.pop();        
    });	
  }

  cancel() {
    this.viewCtrl.dismiss();
  }

}



